.dropDownTitle{
    cursor: pointer; 
    user-select:none;
}
.dropDownTitle:hover{
    color:rgba(0,0,0, 0.5);
}

.hideMind {
    display:none;
    height:0;
}

.showMind{
    display:unset;
    padding-left:2rem;
    border-bottom: 1px solid #a2a2a2!important; 
}
.showMind li{
    border-bottom: 0px solid #a2a2a2!important; 
    padding:15px 0px 15px!important;
}

.hideFroz {
    display:none;
    height:0;
}


.showFroz{
    display:unset;
    padding-left:2rem;
    border-bottom: 1px solid #a2a2a2!important; 
}
.showFroz li{
    border-bottom: 0px solid #a2a2a2!important; 
    padding:15px 0px 15px!important;
}
