.side-drawer {
    
    /*background: white;*/
    /*box-shadow: 2px 0px 5px rgba(0,0,0, 0.5);*/
    position:absolute;
    overflow:hidden;
    overflow-x: hidden;
    top:0;
    left:0;
    width:100%; 
    padding:0;
    z-index: 9997;
    
}

.side-drawer ul {
    list-style:none;
    display:flex;
    flex-direction: column;
    padding: 0 10% 0 10%;
    padding-top: 1.875em;
    margin:0;

}


.side-drawer a {
    color: #808080;
    font-size:1;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
      
}

.side-drawer a:hover ,
.side-drawer a:active {
    color:rgba(0,0,0, 0.5);
}

.side-drawer__items {
    padding-top:60px!important;
}

.side-drawer__items li{
    padding: 30px 0 30px;
    line-height: 1;
    border-bottom: 1px solid #a2a2a2;
      
}
    

.side-drawer__brand li{
    width:94%;
}
.side-drawer__brand a{
    font-size:0.85em;
    letter-spacing: 0.04em;
}

.leaf-container {
    margin-left:25%;
    margin-right:25%;
        
}

.oakleaf {
    margin-top:2.5rem;
    margin-bottom: 4rem;
    width:100%;

}

.break-mobile {
    display:inline-block;
    margin-right:20px;
}

@media (min-width:1025px){
    .side-drawer{
        padding-top:0!important;
        padding-bottom:2rem;
        /*display:none;*/
    }
   
    .side-drawer ul {
       padding-top: 13.5vh;
       
    
       
    }

    .side-drawer__items {
        width:27rem;
    }

    .side-drawer__items li {
        /*width:27rem;*/
        /*width:40rem;*/
       
        width:100%;
       /* text-align:center;*/
        
    }

    .side-drawer__brand li{
        width:100%;
        text-align:left;
        
        
    }

.showMind {
        padding:0.9rem;
        padding-left:2rem;
    }
    .showFroz{
        padding:0.9rem;
        padding-left:2rem;
    }

   

   
    .side-drawer-layout {
        display:flex;
        flex-direction: row-reverse;
    }

    .leaf-container {
        width:20vw;
        margin-right:10%;
        margin-top:8%;
        
        
            
    }
}

